<template>

<nav>
    <router-link to="/">Home</router-link> |
    <router-link to="/about">Proveedor</router-link> |
    <router-link to="/entrada">Entradas</router-link> |
    <router-link to="/inicio">inicio</router-link> |
</nav>

    <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-QWTKZyjpPEjISv5WaRU9OFeRpok6YctnYmDr5pNlyT2bRjXh0JMhjY6hW+ALEwIH" crossorigin="anonymous">

  <div class=" container card ">
    <h2>Agregar Nuevo Proveedor</h2>
    <div class="form-group m-2">
      <label class="col-2" for="_id"> Id</label>
      <input class="col-4" v-model="newProvider._id"  type="text"  id="_id" placeholder="Id">
    </div>
    <div class="form-group m-2">
      <label class="col-2"  for="name">Nombre</label>
      <input class="col-4" v-model="newProvider.name" type="text" id="name" placeholder="Nombre" />
    </div>
    <div class="form-group m-2">
      <label class="col-2" for="phone">Teléfono</label>
      <input class="col-4" v-model="newProvider.phone" type="text" id="phone" placeholder="Teléfono" />
    </div>
    <div class="form-group m-2">
      <label class="col-2" for="email">Correo</label>
      <input class="col-4" v-model="newProvider.email" type="email" id="email" placeholder="Correo" />
    </div>

    <div class="form-group m-2">
      <label class="col-2" for="clave">Clave</label>
      <input class="col-4" v-model="newProvider.clave" type="password" id="clave" placeholder="Clave" />
    </div>

    <div class="m-2">
      <button class="submit-button col-6" :disabled="isButtonDisabled" @click="addNewProvider">Agregar Proveedor</button>
     <button   @click="goToCreateView" class="cancel-button col-3">Cancelar</button></div>
</div>
</template>

<script>

import { getFirestore, collection, addDoc, orderBy, query, onSnapshot } from 'firebase/firestore';
import { initializeApp } from 'firebase/app';
import { getAnalytics } from 'firebase/analytics';
import { ref, onUnmounted } from 'vue';

  const firebaseConfig = {
  apiKey: "AIzaSyA_uWz4tJnzrpgSID-eR--M58jPzsfowDg",
  authDomain: "regirstroproesa.firebaseapp.com",
  databaseURL: "https://regirstroproesa-default-rtdb.firebaseio.com",
  projectId: "regirstroproesa",
  storageBucket: "regirstroproesa.appspot.com",
  messagingSenderId: "973196483901",
  appId: "1:973196483901:web:57e3da488b032279b67954"
};

const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const db = getFirestore(app);
const providers = ref([]);

export default {
  name: 'CreateView',
  data() {
    return {
      newProvider: {
        _id:'',
        name: '',
        phone: '',
        email: '',
        clave: '',
      },
    };
  },
  computed: {
    isFormValid() {
      return (
        this.newProvider._id &&
        this.newProvider.name &&
        this.newProvider.phone &&
        this.newProvider.email &&
        this.newProvider.clave

      );
    },
    isButtonDisabled() {
      return !this.isFormValid;
    },
  },
  mounted() {
    const providersQuery = query(collection(db, 'proveedores'), orderBy('name'));
    const unsubscribe = onSnapshot(providersQuery, (snapshot) => {
      providers.value = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    });
    onUnmounted(unsubscribe);
  },
  methods: {
    goToCreateView() {
      this.$router.push('/proveedores');
    },
    async addNewProvider() {
      try {
        await addDoc(collection(db, 'proveedores'), {

          _id:  this.newProvider._id ,
          name: this.newProvider.name,
          phone: this.newProvider.phone,
          email: this.newProvider.email,
           clave: this.newProvider.clave,


        });
        this.newProvider = {
          _id: '',
          name: '',
          phone: '',
          email: '',
          clave: '',
        };
      } catch (error) {
        console.error('Error adding new provider:', error);
      }
    },
  },
};

</script>